.App {
  font-family: sans-serif;
  text-align: center;
}
.list {
  border: 1px solid blue;
}
.list-content {
  padding: 24px 8px;
  border-top: 1px solid gray;
  background: rgba(200, 200, 200, 0.2);
}
.list-item {
  padding: 8px 16px;
  border: 1px solid gray;
  margin-bottom: 8px;
  background: rgb(200, 200, 200);
  transition: background 0.1s;
}
.list-item:hover {
  background: rgb(235, 235, 235);
}
